import React from 'react';
import { Spinner } from 'reactstrap';
import Loadable from 'react-loadable';

const loading = () => "";

const Main = Loadable({ loader: () => import('./layout/default/Main'), loading });
const Home = Loadable({ loader: () => import('./layout/default-home/Main'), loading });
const Donate = Loadable({ loader: () => import('./pages/Donate'), loading });

const routes = [
  { path: '/', exact:true, name: "", component: Home },
  { path: '/about-me', exact:true, name: "", component: Main },  
  { path: '/donate', name: "", component: Donate }
];

export default routes;
